.add-task-form{display:flex;flex-direction:column;gap:1rem}.add-task-form .test{color:red}.add-task-form .form-control{padding:.5rem 1rem;font-size:1.5rem;border:2px solid #a17cf3;border-radius:10px;background-color:#2c2c2c;color:#fff}.add-task-form .form-control::placeholder{color:#a5a5a5}.add-task-form .form-control:focus{border-color:#c599ff;outline:none;box-shadow:0 0 5px rgba(193,153,255,.5)}.add-task-form .btn-primary{display:flex;justify-content:center;align-items:center;width:50px;height:50px;background-color:#a17cf3;color:#fff;font-size:1.5rem}.add-task-form .btn-primary:hover{background-color:#8c6dcb;transform:scale(1.1);transition:all .3s ease-in-out}.add-task-form .btn-primary:active{background-color:#6a4eb2;transform:scale(0.95)}